import React, { Component } from 'react';
import PropTypes from 'prop-types';
import utils from '../../common/util.d';

/**
 * selectedTitleStyle: 
 * title: 
 * titleStyle: 
 * name: key
 * tabStyle: tabBar.Item
 * icon: 
 * selectedIcon: 
 * changeTab: itemnamekey
 */

export default class TabBarItem extends Component{
    //selected from TabBar
    static defaultProps = {
        //selected: false,
        selectedTitleStyle: {},
        title: '',
        titleStyle: {},
        name: '',
        tabStyle: {},
        icon: null,
        selectedIcon: null,
        changeTab: () => {}
    }
    static propTypes = {
        //selected: PropTypes.bool,
        selectedTitleStyle: PropTypes.object,
        title: PropTypes.string,
        titleStyle: PropTypes.object,
        name: PropTypes.string,
        tabStyle: PropTypes.object,
        icon: PropTypes.element,
        selectedIcon: PropTypes.element,
        changeTab: PropTypes.func
    }

    constructor(props){
        super(props);
    }
    
    render(){
        const {
            selected, 
            selectedTitleStyle,
            titleStyle,
            tabStyle,
            name,
            selectedIcon,
            icon,
            title,
            changeTab
        } = this.props;
        const textStyle = selected ? selectedTitleStyle : titleStyle;
        return (
            <a
                className = {`x-tabbar-item${selected ? ' x-tabbar-item-active' : ''}`}
                href = {'javascript:;'}
                style = {[textStyle, tabStyle]}
                onClick = {() => changeTab(name)}
            >
                {selected ? selectedIcon : icon}
                {/*in order to use flex-column*/}
                <span>{title}</span>
            </a>
        )
    }
}



